// JWT ÌÜ†ÌÅ∞ÏùÑ Ïø†ÌÇ§ÏóêÏÑú ÏßÅÏ†ë ÌååÏã±ÌïòÏó¨ Ï¶âÏãú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï∂îÏ∂ú

import type { User } from '@supabase/supabase-js';

/**
 * JWT ÌÜ†ÌÅ∞ÏùÑ ÎîîÏΩîÎî©ÌïòÏó¨ payload Ï∂îÏ∂ú
 */
function decodeJWTPayload(token: string): any | null {
  try {
    const parts = token.split('.');
    if (parts.length !== 3) return null;

    // Base64 URL ÎîîÏΩîÎî©
    const payload = parts[1]
      .replace(/-/g, '+')
      .replace(/_/g, '/');
    
    // Ìå®Îî© Ï∂îÍ∞Ä
    const padded = payload + '='.repeat((4 - payload.length % 4) % 4);
    
    return JSON.parse(atob(padded));
  } catch (error) {
    console.warn('üîç [JWT Parser] JWT ÎîîÏΩîÎî© Ïã§Ìå®:', error);
    return null;
  }
}

/**
 * Ïø†ÌÇ§ÏóêÏÑú Supabase Ïù∏Ï¶ù ÌÜ†ÌÅ∞ Ï∞æÍ∏∞ (Î°úÏª¨ ÌôòÍ≤Ω ÎåÄÏùë Í∞ïÌôî)
 */
function getSupabaseTokenFromCookies(): string | null {
  if (typeof document === 'undefined') return null;

  try {
    console.log('üîç [JWT Parser] Ïø†ÌÇ§ Í≤ÄÏÉâ ÏãúÏûë (Î°úÏª¨ ÌôòÍ≤Ω ÎåÄÏùë)');
    
    // ÌôòÍ≤Ω Ï†ïÎ≥¥ ÌôïÏù∏
    const isLocal = window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1';
    const protocol = window.location.protocol;
    const hostname = window.location.hostname;
    
    console.log('üåê [JWT Parser] ÌôòÍ≤Ω Ï†ïÎ≥¥:', {
      hostname,
      protocol,
      isLocal,
      port: window.location.port
    });

    // Supabase ÌîÑÎ°úÏ†ùÌä∏ ID Ï∂îÏ∂ú (Ïó¨Îü¨ Î∞©Î≤ï ÏãúÎèÑ)
    const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
    if (!supabaseUrl) {
      console.warn('üîç [JWT Parser] NEXT_PUBLIC_SUPABASE_URL ÏóÜÏùå');
      return null;
    }
    
    console.log('üîó [JWT Parser] Supabase URL:', supabaseUrl.substring(0, 30) + '...');
    
    const urlParts = supabaseUrl.split('.');
    const projectId = urlParts[0]?.split('://')[1];
    if (!projectId) {
      console.warn('üîç [JWT Parser] ÌîÑÎ°úÏ†ùÌä∏ ID Ï∂îÏ∂ú Ïã§Ìå®');
      return null;
    }

    console.log('üè∑Ô∏è [JWT Parser] ÌîÑÎ°úÏ†ùÌä∏ ID:', projectId);

    // Î™®Îì† Ïø†ÌÇ§ Î°úÍπÖ (ÎîîÎ≤ÑÍπÖÏö©)
    const allCookies = document.cookie.split(';');
    console.log('üç™ [JWT Parser] Ï†ÑÏ≤¥ Ïø†ÌÇ§ Î™©Î°ù:');
    allCookies.forEach((cookie, index) => {
      const [name] = cookie.trim().split('=');
      console.log(`  ${index + 1}. ${name}`);
    });

    // üéØ Î∂ÑÌï†Îêú Ïø†ÌÇ§ Ïö∞ÏÑ† Ï≤òÎ¶¨ (.0, .1, .2 Îì±)
    const chunkPattern = `sb-${projectId}-auth-token`;
    const chunks: { [key: string]: string } = {};
    
    console.log('üß© [JWT Parser] Î∂ÑÌï†Îêú Ïø†ÌÇ§ Í≤ÄÏÉâ:', chunkPattern);
    
    for (const cookie of allCookies) {
      const [name, value] = cookie.trim().split('=');
      
      if (name && name.startsWith(chunkPattern) && value) {
        // Î∂ÑÌï†Îêú Ïø†ÌÇ§ Ìå®ÌÑ¥ ÌôïÏù∏ (.0, .1, .2 Îì±)
        const chunkMatch = name.match(/\.(\d+)$/);
        if (chunkMatch) {
          const chunkIndex = chunkMatch[1];
          chunks[chunkIndex] = decodeURIComponent(value);
          console.log(`üß© [JWT Parser] Ïø†ÌÇ§ Ï°∞Í∞Å Î∞úÍ≤¨: ${name} (${value.length}Ïûê)`);
        }
      }
    }

    // Î∂ÑÌï†Îêú Ïø†ÌÇ§ Ï°∞Ìï©
    if (Object.keys(chunks).length > 0) {
      console.log('üîß [JWT Parser] Î∂ÑÌï†Îêú Ïø†ÌÇ§ Ï°∞Ìï© ÏãúÏûë:', Object.keys(chunks).sort());
      
      // ÏàúÏÑúÎåÄÎ°ú Ï†ïÎ†¨ÌïòÏó¨ Ï°∞Ìï©
      const sortedChunkKeys = Object.keys(chunks).sort((a, b) => parseInt(a) - parseInt(b));
      let combinedValue = '';
      
      for (const key of sortedChunkKeys) {
        combinedValue += chunks[key];
        console.log(`üîß [JWT Parser] Ï°∞Í∞Å ${key} Ï∂îÍ∞Ä: ${chunks[key].substring(0, 20)}... (Ï¥ù Í∏∏Ïù¥: ${combinedValue.length})`);
      }
      
      console.log('‚úÖ [JWT Parser] Î∂ÑÌï†Îêú Ïø†ÌÇ§ Ï°∞Ìï© ÏôÑÎ£å:', {
        totalChunks: sortedChunkKeys.length,
        totalLength: combinedValue.length,
        preview: combinedValue.substring(0, 50) + '...'
      });

      // base64- Ï†ëÎëêÏÇ¨ Ï†úÍ±∞ Î∞è ÎîîÏΩîÎî© ÏãúÎèÑ
      let processedValue = combinedValue;
      
      if (processedValue.startsWith('base64-')) {
        console.log('üîç [JWT Parser] base64- Ï†ëÎëêÏÇ¨ Î∞úÍ≤¨, Ï†úÍ±∞ ÌõÑ ÎîîÏΩîÎî© ÏãúÎèÑ');
        const base64Data = processedValue.substring(7); // 'base64-' Ï†úÍ±∞
        
        try {
          processedValue = atob(base64Data);
          console.log('‚úÖ [JWT Parser] base64 ÎîîÏΩîÎî© ÏÑ±Í≥µ:', {
            ÏõêÎ≥∏Í∏∏Ïù¥: base64Data.length,
            ÎîîÏΩîÎî©ÌõÑÍ∏∏Ïù¥: processedValue.length,
            preview: processedValue.substring(0, 50) + '...'
          });
        } catch (base64Error) {
          console.warn('‚ö†Ô∏è [JWT Parser] base64 ÎîîÏΩîÎî© Ïã§Ìå®, ÏõêÎ≥∏ ÏÇ¨Ïö©:', base64Error);
        }
      }

      // JWT Ìå®ÌÑ¥ ÌôïÏù∏
      if (processedValue.startsWith('eyJ')) {
        console.log('üéØ [JWT Parser] Î∂ÑÌï†Îêú Ïø†ÌÇ§ÏóêÏÑú JWT Î∞úÍ≤¨!');
        return processedValue;
      }

      // JSON ÌååÏã± ÏãúÎèÑ
      try {
        const parsed = JSON.parse(processedValue);
        console.log('‚úÖ [JWT Parser] Î∂ÑÌï†Îêú Ïø†ÌÇ§ JSON ÌååÏã± ÏÑ±Í≥µ:', {
          hasAccessToken: !!parsed.access_token,
          hasRefreshToken: !!parsed.refresh_token,
          tokenType: parsed.token_type
        });
        
        if (parsed.access_token) {
          console.log('üéØ [JWT Parser] Î∂ÑÌï†Îêú Ïø†ÌÇ§ÏóêÏÑú access_token Ï∂îÏ∂ú ÏÑ±Í≥µ!');
          return parsed.access_token;
        }
      } catch (jsonError) {
        console.warn('‚ö†Ô∏è [JWT Parser] Î∂ÑÌï†Îêú Ïø†ÌÇ§ JSON ÌååÏã± Ïã§Ìå®:', jsonError);
      }
    }

    // Îã§ÏñëÌïú Ìå®ÌÑ¥ÏúºÎ°ú ÌÜ†ÌÅ∞ Í≤ÄÏÉâ (Í∏∞Ï°¥ Î°úÏßÅ)
    const tokenPatterns = [
      `sb-${projectId}-auth-token`, // Í∏∞Î≥∏ Ìå®ÌÑ¥
      `supabase-auth-token`, // ÎåÄÏïà Ìå®ÌÑ¥
      `sb-auth-token` // Îã®Ïàú Ìå®ÌÑ¥
    ];

    console.log('üîç [JWT Parser] Í≤ÄÏÉâÌï† ÌÜ†ÌÅ∞ Ìå®ÌÑ¥Îì§:', tokenPatterns);
    
    for (const pattern of tokenPatterns) {
      console.log(`üîé [JWT Parser] "${pattern}" Ìå®ÌÑ¥ÏúºÎ°ú Í≤ÄÏÉâ Ï§ë...`);
      
      for (const cookie of allCookies) {
        const [name, value] = cookie.trim().split('=');
        
        if (name && (name === pattern || name.startsWith(pattern)) && value) {
          // Î∂ÑÌï†Îêú Ïø†ÌÇ§Îäî Ïù¥ÎØ∏ Ï≤òÎ¶¨ÌñàÏúºÎØÄÎ°ú Í±¥ÎÑàÎõ∞Í∏∞
          if (name.includes('.')) continue;
          
          console.log(`‚úÖ [JWT Parser] Ïø†ÌÇ§ Îß§Ïπ≠: ${name}`);
          
          try {
            // URL ÎîîÏΩîÎî© ÌõÑ JSON ÌååÏã±
            const decoded = decodeURIComponent(value);
            console.log('üîç [JWT Parser] ÎîîÏΩîÎî©Îêú Ïø†ÌÇ§ Í∏∏Ïù¥:', decoded.length);
            
            let parsed: any;
            
            // JSON ÌååÏã± ÏãúÎèÑ
            try {
              parsed = JSON.parse(decoded);
              console.log('‚úÖ [JWT Parser] JSON ÌååÏã± ÏÑ±Í≥µ:', {
                hasAccessToken: !!parsed.access_token,
                hasRefreshToken: !!parsed.refresh_token,
                tokenType: parsed.token_type,
                expiresAt: parsed.expires_at
              });
            } catch (jsonError) {
              // JSONÏù¥ ÏïÑÎãå Í≤ΩÏö∞ ÏßÅÏ†ë ÌÜ†ÌÅ∞Ïùº Ïàò ÏûàÏùå
              console.log('üîç [JWT Parser] JSON ÌååÏã± Ïã§Ìå®, ÏßÅÏ†ë ÌÜ†ÌÅ∞ÏúºÎ°ú ÏãúÎèÑ');
              
              // JWT Ìå®ÌÑ¥ ÌôïÏù∏ (eyJÎ°ú ÏãúÏûëÌïòÎäîÏßÄ)
              if (decoded.startsWith('eyJ')) {
                console.log('‚úÖ [JWT Parser] ÏßÅÏ†ë JWT ÌÜ†ÌÅ∞ Î∞úÍ≤¨');
                return decoded;
              }
              
              console.warn('üîç [JWT Parser] JSON ÌååÏã± Ïã§Ìå®:', jsonError);
              continue;
            }
            
            // access_token Ï∂îÏ∂ú
            if (parsed.access_token) {
              console.log('üç™ [JWT Parser] Ïø†ÌÇ§ÏóêÏÑú ÌÜ†ÌÅ∞ Ï∂îÏ∂ú ÏÑ±Í≥µ');
              return parsed.access_token;
            } else {
              console.warn('üîç [JWT Parser] access_token ÌïÑÎìú ÏóÜÏùå:', Object.keys(parsed));
            }
          } catch (parseError) {
            console.warn('üîç [JWT Parser] Ïø†ÌÇ§ ÌååÏã± Ïã§Ìå®:', parseError);
          }
        }
      }
    }

    // Î°úÏª¨ ÌôòÍ≤Ω ÌäπÎ≥Ñ Ï≤òÎ¶¨: Î™®Îì† sb- Ïø†ÌÇ§ Í≤ÄÏÉâ (Î∂ÑÌï† Ïø†ÌÇ§ Ï†úÏô∏)
    if (isLocal) {
      console.log('üè† [JWT Parser] Î°úÏª¨ ÌôòÍ≤Ω - Î™®Îì† sb- Ïø†ÌÇ§ Í≤ÄÏÉâ');
      
      for (const cookie of allCookies) {
        const [name, value] = cookie.trim().split('=');
        
        if (name && name.includes('sb-') && name.includes('auth') && !name.includes('.') && value) {
          console.log(`üîç [JWT Parser] Î°úÏª¨ ÌôòÍ≤ΩÏóêÏÑú Î∞úÍ≤¨Îêú sb- Ïø†ÌÇ§: ${name}`);
          
          try {
            const decoded = decodeURIComponent(value);
            
            // JWT Ìå®ÌÑ¥ ÌôïÏù∏
            if (decoded.startsWith('eyJ')) {
              console.log('‚úÖ [JWT Parser] Î°úÏª¨ ÌôòÍ≤ΩÏóêÏÑú ÏßÅÏ†ë JWT Î∞úÍ≤¨');
              return decoded;
            }
            
            // JSON ÌååÏã± ÏãúÎèÑ
            try {
              const parsed = JSON.parse(decoded);
              if (parsed.access_token) {
                console.log('‚úÖ [JWT Parser] Î°úÏª¨ ÌôòÍ≤ΩÏóêÏÑú ÌÜ†ÌÅ∞ Ï∂îÏ∂ú ÏÑ±Í≥µ');
                return parsed.access_token;
              }
            } catch {
              // JSONÏù¥ ÏïÑÎãàÎ©¥ Î¨¥Ïãú
            }
          } catch (error) {
            console.warn('üîç [JWT Parser] Î°úÏª¨ Ïø†ÌÇ§ ÌååÏã± Ïò§Î•ò:', error);
          }
        }
      }
    }

    console.log('‚ùå [JWT Parser] Ïú†Ìö®Ìïú ÌÜ†ÌÅ∞ÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏùå');
    return null;
  } catch (error) {
    console.warn('üîç [JWT Parser] Ïø†ÌÇ§ Í≤ÄÏÉâ Ï§ë Ïò§Î•ò:', error);
    return null;
  }
}

/**
 * localStorageÏóêÏÑú Supabase ÌÜ†ÌÅ∞ Ï∞æÍ∏∞
 */
function getSupabaseTokenFromStorage(): string | null {
  try {
    console.log('üîç [JWT Parser] localStorage Í≤ÄÏÉâ ÏãúÏûë');
    
    const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
    if (!supabaseUrl) {
      console.warn('üîç [JWT Parser] NEXT_PUBLIC_SUPABASE_URL ÏóÜÏùå (localStorage)');
      return null;
    }
    
    const urlParts = supabaseUrl.split('.');
    const projectId = urlParts[0]?.split('://')[1];
    
    if (projectId) {
      const key = `sb-${projectId}-auth-token`;
      const storedData = localStorage.getItem(key);
      
      if (storedData) {
        console.log(`‚úÖ [JWT Parser] localStorageÏóêÏÑú ÌÜ†ÌÅ∞ Î∞úÍ≤¨: ${key}`);
        
        try {
          const parsed = JSON.parse(storedData);
          if (parsed?.access_token) {
            console.log('‚úÖ [JWT Parser] localStorageÏóêÏÑú access_token Ï∂îÏ∂ú ÏÑ±Í≥µ');
            return parsed.access_token;
          } else {
            console.warn('üîç [JWT Parser] localStorage Îç∞Ïù¥ÌÑ∞Ïóê access_token ÏóÜÏùå:', Object.keys(parsed));
          }
        } catch (parseError) {
          console.warn('‚ö†Ô∏è [JWT Parser] localStorage Îç∞Ïù¥ÌÑ∞ ÌååÏã± Ïã§Ìå®:', parseError);
          // ÏßÅÏ†ë JWTÏùº Ïàò ÏûàÏùå
          if (storedData.startsWith('eyJ')) {
            console.log('‚úÖ [JWT Parser] localStorageÏóêÏÑú ÏßÅÏ†ë JWT Î∞úÍ≤¨');
            return storedData;
          }
        }
      }
    }

    // Î™®Îì† Supabase ÌÇ§ ÌôïÏù∏
    console.log('üîç [JWT Parser] Î™®Îì† localStorage sb- ÌÇ§ Í≤ÄÏÉâ');
    const allKeys: string[] = [];
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key && key.startsWith('sb-') && key.endsWith('-auth-token')) {
        allKeys.push(key);
        
        try {
          const storedData = localStorage.getItem(key);
          if (storedData) {
            console.log(`üîç [JWT Parser] localStorage ÌÇ§ ÌôïÏù∏: ${key}`);
            
            // JWT ÏßÅÏ†ë ÌôïÏù∏
            if (storedData.startsWith('eyJ')) {
              console.log(`‚úÖ [JWT Parser] localStorageÏóêÏÑú ÏßÅÏ†ë JWT Î∞úÍ≤¨: ${key}`);
              return storedData;
            }
            
            // JSON ÌååÏã± ÏãúÎèÑ
            try {
              const parsed = JSON.parse(storedData);
              if (parsed?.access_token) {
                console.log(`‚úÖ [JWT Parser] localStorageÏóêÏÑú ÌÜ†ÌÅ∞ Ï∂îÏ∂ú ÏÑ±Í≥µ: ${key}`);
                return parsed.access_token;
              }
            } catch {
              // JSONÏù¥ ÏïÑÎãàÎ©¥ Î¨¥Ïãú
            }
          }
        } catch (error) {
          console.warn(`‚ö†Ô∏è [JWT Parser] localStorage ÌÇ§ Ï≤òÎ¶¨ Ïã§Ìå®: ${key}`, error);
        }
      }
    }

    console.log('üîç [JWT Parser] localStorage Í≤ÄÏÉâÎêú ÌÇ§:', allKeys);
    console.log('‚ùå [JWT Parser] localStorageÏóêÏÑú Ïú†Ìö®Ìïú ÌÜ†ÌÅ∞ÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏùå');
    return null;
  } catch (error) {
    console.error('‚ùå [JWT Parser] localStorage Í≤ÄÏÉâ Ï§ë Ïò§Î•ò:', error);
    return null;
  }
}

/**
 * JWT ÌÜ†ÌÅ∞ÏóêÏÑú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï∂îÏ∂ú
 */
function extractUserFromJWT(token: string): User | null {
  try {
    const payload = decodeJWTPayload(token);
    if (!payload) return null;

    console.log('üîç [JWT Parser] JWT payload ÌôïÏù∏:', {
      sub: payload.sub?.substring(0, 8) + '...',
      email: payload.email,
      exp: payload.exp,
      iat: payload.iat,
      provider: payload.app_metadata?.provider
    });

    // JWT payloadÏóêÏÑú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï∂îÏ∂ú
    const user: User = {
      id: payload.sub,
      email: payload.email,
      user_metadata: payload.user_metadata || {},
      app_metadata: payload.app_metadata || {},
      aud: payload.aud,
      created_at: payload.created_at || new Date().toISOString(),
      updated_at: payload.updated_at || new Date().toISOString(),
      email_confirmed_at: payload.email_confirmed_at,
      phone_confirmed_at: payload.phone_confirmed_at,
      confirmation_sent_at: payload.confirmation_sent_at,
      recovery_sent_at: payload.recovery_sent_at,
      invited_at: payload.invited_at,
      action_link: payload.action_link,
      role: payload.role
    };

    // ÌÜ†ÌÅ∞ ÎßåÎ£å ÏãúÍ∞Ñ ÌôïÏù∏
    const now = Math.floor(Date.now() / 1000);
    if (payload.exp && payload.exp < now) {
      console.warn('üîç [JWT Parser] ÌÜ†ÌÅ∞Ïù¥ ÎßåÎ£åÎê®:', {
        exp: payload.exp,
        now,
        expired: payload.exp < now,
        expiredSecondsAgo: now - payload.exp
      });
      return null;
    }

    console.log('‚úÖ [JWT Parser] ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï∂îÏ∂ú ÏÑ±Í≥µ:', {
      userId: user.id?.substring(0, 8) + '...',
      email: user.email,
      provider: user.app_metadata?.provider,
      exp: payload.exp ? new Date(payload.exp * 1000).toISOString() : 'N/A'
    });

    return user;
  } catch (error) {
    console.warn('üîç [JWT Parser] ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï∂îÏ∂ú Ïã§Ìå®:', error);
    return null;
  }
}

/**
 * Ïø†ÌÇ§ÏôÄ localStorageÏóêÏÑú Ï¶âÏãú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï∂îÏ∂ú (ÎÑ§Ìä∏ÏõåÌÅ¨ ÏöîÏ≤≠ ÏóÜÏùå)
 */
export function getInstantUserFromCookies(): User | null {
  console.log('üöÄ [JWT Parser] Ï¶âÏãú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï∂îÏ∂ú ÏãúÏûë (Ïø†ÌÇ§ + localStorage)');
  
  // 1ÏàúÏúÑ: Ïø†ÌÇ§ÏóêÏÑú ÌÜ†ÌÅ∞ Ï∞æÍ∏∞
  let token = getSupabaseTokenFromCookies();
  
  // 2ÏàúÏúÑ: localStorageÏóêÏÑú ÌÜ†ÌÅ∞ Ï∞æÍ∏∞
  if (!token) {
    console.log('üîÑ [JWT Parser] Ïø†ÌÇ§ÏóêÏÑú ÌÜ†ÌÅ∞ ÏóÜÏùå ‚Üí localStorage ÌôïÏù∏');
    token = getSupabaseTokenFromStorage();
  }
  
  if (!token) {
    console.log('‚ùå [JWT Parser] Ïø†ÌÇ§ÏôÄ localStorage Î™®ÎëêÏóêÏÑú ÌÜ†ÌÅ∞ ÏóÜÏùå');
    return null;
  }

  console.log('üéØ [JWT Parser] ÌÜ†ÌÅ∞ Î∞úÍ≤¨ - JWT ÌååÏã± ÏãúÏûë');
  return extractUserFromJWT(token);
}

/**
 * ÌÜ†ÌÅ∞ ÎßåÎ£å ÏãúÍ∞Ñ ÌôïÏù∏
 */
export function getTokenExpiry(): Date | null {
  const token = getSupabaseTokenFromCookies();
  if (!token) return null;

  const payload = decodeJWTPayload(token);
  if (!payload?.exp) return null;

  return new Date(payload.exp * 1000);
}

/**
 * ÌÜ†ÌÅ∞Ïù¥ Í≥ß ÎßåÎ£åÎêòÎäîÏßÄ ÌôïÏù∏ (30Î∂Ñ Ïù¥ÎÇ¥)
 */
export function isTokenExpiringSoon(): boolean {
  const expiry = getTokenExpiry();
  if (!expiry) return false;

  const now = new Date();
  const thirtyMinutes = 30 * 60 * 1000; // 30Î∂Ñ
  
  return (expiry.getTime() - now.getTime()) < thirtyMinutes;
}

/**
 * Í∞úÎ∞úÏûê ÎîîÎ≤ÑÍπÖ Ìï®Ïàò - Î∏åÎùºÏö∞Ï†Ä ÏΩòÏÜîÏö© (Î°úÏª¨ ÌôòÍ≤Ω ÌäπÌôî)
 */
export function debugJWTInfo() {
  if (typeof window === 'undefined') return;

  console.log('üîç [JWT Debug] ÌôòÍ≤Ω Ï†ïÎ≥¥:', {
    hostname: window.location.hostname,
    protocol: window.location.protocol,
    port: window.location.port,
    isLocal: window.location.hostname === 'localhost',
    supabaseUrl: process.env.NEXT_PUBLIC_SUPABASE_URL?.substring(0, 30) + '...'
  });

  // Î™®Îì† Ïø†ÌÇ§ Ï∂úÎ†•
  const allCookies = document.cookie.split(';');
  console.log('üç™ [JWT Debug] Î™®Îì† Ïø†ÌÇ§:', allCookies.map(c => c.trim().split('=')[0]));

  // localStorage Ï†ïÎ≥¥ Ï∂úÎ†•
  console.log('üíæ [JWT Debug] localStorage Ï†ïÎ≥¥:');
  const localStorageKeys: string[] = [];
  const supabaseKeys: string[] = [];
  for (let i = 0; i < localStorage.length; i++) {
    const key = localStorage.key(i);
    if (key) {
      localStorageKeys.push(key);
      if (key.startsWith('sb-') && key.includes('auth')) {
        supabaseKeys.push(key);
      }
    }
  }
  console.log(`üíæ [JWT Debug] Ï¥ù localStorage ÌÇ§: ${localStorageKeys.length}Í∞ú, Supabase Ïù∏Ï¶ù ÌÇ§: ${supabaseKeys.length}Í∞ú`);

  // Ïø†ÌÇ§ÏóêÏÑú ÌÜ†ÌÅ∞ ÏãúÎèÑ
  const cookieToken = getSupabaseTokenFromCookies();
  console.log('üç™ [JWT Debug] Ïø†ÌÇ§ ÌÜ†ÌÅ∞:', cookieToken ? '‚úÖ Î∞úÍ≤¨' : '‚ùå ÏóÜÏùå');
  
  // localStorageÏóêÏÑú ÌÜ†ÌÅ∞ ÏãúÎèÑ
  const storageToken = getSupabaseTokenFromStorage();
  console.log('üíæ [JWT Debug] localStorage ÌÜ†ÌÅ∞:', storageToken ? '‚úÖ Î∞úÍ≤¨' : '‚ùå ÏóÜÏùå');

  // ÏµúÏ¢Ö ÌÜ†ÌÅ∞ Í≤∞Ï†ï
  const token = cookieToken || storageToken;
  if (!token) {
    console.log('‚ùå [JWT Debug] ÌÜ†ÌÅ∞ ÏóÜÏùå (Ïø†ÌÇ§ÏôÄ localStorage Î™®Îëê)');
    return { success: false, message: 'ÌÜ†ÌÅ∞ ÏóÜÏùå' };
  }

  console.log('üéØ [JWT Debug] ÏµúÏ¢Ö ÏÑ†ÌÉùÎêú ÌÜ†ÌÅ∞ ÏÜåÏä§:', cookieToken ? 'Ïø†ÌÇ§' : 'localStorage');

  const payload = decodeJWTPayload(token);
  const user = extractUserFromJWT(token);
  const expiry = getTokenExpiry();
  const expiringSoon = isTokenExpiringSoon();

  const result = {
    success: true,
    tokenSource: cookieToken ? 'cookie' : 'localStorage',
    token: {
      length: token.length,
      prefix: token.substring(0, 20) + '...',
      isJWT: token.startsWith('eyJ')
    },
    payload: payload ? {
      sub: payload.sub?.substring(0, 8) + '...',
      email: payload.email,
      exp: payload.exp,
      iat: payload.iat,
      provider: payload.app_metadata?.provider,
      isExpired: payload.exp ? (Math.floor(Date.now() / 1000) > payload.exp) : null
    } : null,
    user: user ? {
      id: user.id?.substring(0, 8) + '...',
      email: user.email,
      provider: user.app_metadata?.provider
    } : null,
    expiry: expiry?.toISOString(),
    expiringSoon,
    isValid: !!user
  };

  console.log('üîç [JWT Debug] ÏôÑÏ†ÑÌïú ÌÜ†ÌÅ∞ Ï†ïÎ≥¥:', result);
  return result;
}

/**
 * Î°úÏª¨ ÌôòÍ≤Ω Ï†ÑÏö© Ïø†ÌÇ§ Î¶¨Ïä§Ìä∏ Ìï®Ïàò
 */
export function debugLocalCookies() {
  if (typeof window === 'undefined') return;

  console.log('üè† [Local Debug] Î°úÏª¨ ÌôòÍ≤Ω Ïø†ÌÇ§ Î∂ÑÏÑù');
  
  const cookies = document.cookie.split(';');
  const analysis = {
    total: cookies.length,
    supabaseCookies: [],
    authCookies: [],
    otherCookies: []
  } as any;

  cookies.forEach(cookie => {
    const [name, value] = cookie.trim().split('=');
    
    if (name.includes('sb-')) {
      analysis.supabaseCookies.push({
        name,
        valueLength: value?.length || 0,
        hasValue: !!value,
        isJWT: value?.startsWith('eyJ') || false
      });
    } else if (name.includes('auth')) {
      analysis.authCookies.push({ name, valueLength: value?.length || 0 });
    } else {
      analysis.otherCookies.push(name);
    }
  });

  console.log('üè† [Local Debug] Ïø†ÌÇ§ Î∂ÑÏÑù Í≤∞Í≥º:', analysis);
  return analysis;
}

// Î∏åÎùºÏö∞Ï†Ä ÌôòÍ≤ΩÏóêÏÑú ÎîîÎ≤ÑÍπÖ Ìï®Ïàò Îì±Î°ù
if (typeof window !== 'undefined') {
  (window as any).debugJWT = debugJWTInfo;
  (window as any).debugLocalCookies = debugLocalCookies;
  console.log('üõ†Ô∏è [JWT Parser] ÎîîÎ≤ÑÍπÖ Ìï®Ïàò Îì±Î°ù:');
  console.log('  - debugJWT() : JWT ÌÜ†ÌÅ∞ Ï†ïÎ≥¥ ÌôïÏù∏');
  console.log('  - debugLocalCookies() : Î°úÏª¨ ÌôòÍ≤Ω Ïø†ÌÇ§ Î∂ÑÏÑù');
} 